<template>
	<div id="chatInfo">
		<similarHeader title='聊天信息' @goBack='goBack' :haveMore='false'></similarHeader>
		<div class='chatInfoTop bgWhite'>
			<div @click='goUserInfo'>
				<img :src="userAvator" class='avator'>
				<p class='username'>{{userName}}</p>
			</div>
			<img src="~img/images/chat/addUser.png" alt="">

		</div>
		<ul class='chatInfoList bgWhite'>
			<li class='borBottom'>查找聊天记录</li>
			<li class='swich'>消息免打扰  
				<van-switch
				  v-model="checked1"
				  active-color="#07c160"
				  inactive-color="#999"
				/>
			</li>
			<li class='swich'>置顶聊天
				<van-switch
				  v-model="checked2"
				  active-color="#07c160"
				  inactive-color="#999"
				/>
			</li>
			<li class='swich'>强提醒
				<van-switch
				  v-model="checked3"
				  active-color="#07c160"
				  inactive-color="#999"
				/>
			</li>
			<li class='borTop'>设置当前聊天背景</li>
			<li class='borTop'>清空聊天记录</li>
			<li class='borTop'>投诉</li>
		</ul>
	</div>
</template>
<script>
	import  similarHeader from '@/components/similar-header'
	export default{
		data(){
			return{
				checked1:false,
				checked2:false,
				checked3:false,
				userName:'',
				userAvator:''
			}
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			goUserInfo(){
				this.$router.push('/userInfo');
			}
		},
		activated(){
			var nickName=sessionStorage.getItem('nowChatNickname');
			if(nickName){
				this.userName=nickName;
			}
			else{
				this.userName=sessionStorage.getItem('nowChatUser');
			}
			this.userAvator=sessionStorage.getItem('nowUserAvator');
		},
		components:{
			similarHeader
		}
	}
</script>
<style  scoped>
	#chatInfo{
		background: #ededed;
		height: 100vh;
	}
	.bgWhite{
		background: #fff;
	}

	.chatInfoTop{
		padding: .3rem 0 .16rem .25rem;
		display: flex;
	}
	.chatInfoTop>div{
		width: 1.3rem;
		text-align: center;
	}
	.avator{
		border-radius: .14rem;
	}
	.chatInfoTop>img{
		margin-left: .5rem;
	}
	.chatInfoTop img{
		width: 1.1rem;
		height: 1.1rem;
	}
	.chatInfoTop p{
		font-size: .32rem;
		overflow: hidden;
		text-align: center;
	    white-space: nowrap;
	    margin-top: -.18rem;
	    color:#9c9c9c;
	    text-overflow: ellipsis;
	}
	.chatInfoList{
		margin-top:.2rem;
		font-size: .38rem;
	}
	.chatInfoList li{
		padding: .2rem .3rem;
	}
	.chatInfoList li:nth-child(2),.chatInfoList li:nth-child(3){
		border-bottom:1px solid #ededed;
		padding-left: 0;
		margin-left: .3rem;
	}
	.borBottom{
		border-bottom: .2rem solid #ededed;
	}
	.borTop{
		border-top: .2rem solid #ededed;	
	}
	.borTop,.borBottom{
		background: url('~img/images/right.png') no-repeat 92vw center/.3rem;
	}
	.chatInfoList li .van-switch{
		position: absolute;
		right: .32rem;
		bottom:.2rem;
		font-size: .4rem !important;
	}
	.swich{
		position: relative;
	}
</style>


